import {
	getAlbum,
	deleteDyn
} from '../../api/api.js';

export default {
	data() {
		return {
			dynInfo: '',
			imgUrl:[]
		}
	},
	methods: {
		getDynInfo() {
			getAlbum().then(data => {
			
				this.dynInfo = data;
			
				
				for (let i = 0; i < this.dynInfo.length; i++) {
					this.imgUrl[i] = this.dynInfo[i].images;
				}
				
			
			});
		},
		preview(index) {
			
			// console.log(imgUrl)
			// uni.previewImage({
			// 	itemList: ['保存圖片', '刪除', "取消"],
			// 	current: index,
			// 	urls: imgUrl,
			// });
			
			let uid = []
			for (let i = 0; i < this.dynInfo.length; i++) {
				uid[i] = this.dynInfo[i].id;
			}
			// console.log(uid)
			uni.previewImage({
				urls: this.imgUrl,
				current: index,
				longPressActions: {
					itemList: ['刪除'],
					success: function(data) {
						// console.log(uid[data.index])
						deleteDyn(uid[data.index]).then(data=>{
							uni.showToast({
								icon: 'none',
								title: '刪除成功'
							});
							uni.navigateTo({
								url: 'album'
							})
							this.getDynInfo()
						})
					},
					fail: function(err) {	
						console.log(err.errMsg);
					}
				}
			});

		}

	},
	onLoad(option) {
		this.uid = option.uid;
	},
	onShow() {
		this.getDynInfo();
	}
}
